<template>
  <div class="signup">
    <h2>Please Sign Up</h2>
    <!-- @submit = v-on:submit -->
    <!-- prevent: 새로고침 방지 -->
    <form @submit.prevent="fnSignup">
      <p>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<label for="name">name: </label>
        <input class="w3-input" name="name" placeholder="Enter name" v-model="name">
      </p>
      <p>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<label for="userId">id: </label>
        <!-- v-model: data 속성과 연결 -->
        <input class="w3-input" name="uid" placeholder="Enter ID" v-model="user_id">
      </p>
      <p>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<label for="password">password: </label>
        <input name="password" class="w3-input" placeholder="Enter password" v-model="user_pw" type="text">
      </p>
      <p>
        <label for="passwordCheck">password check: </label>
        <input name="passwordCheck" class="w3-input" placeholder="Re-enter password" v-model="passwordCheck" type="text">
      </p>
      <button class="w3-button w3-green w3-round" type="submit">SignUp</button>
      <!-- 결과 메시지 출력 -->
      <p style="font-family: 나눔고딕">{{logMessage}}</p>
    </form>
  </div>
</template>

<script>
//import axios from "axios";

import axios from "axios";

export default {
  data() {
    return {
      name: '',
      user_id: '',
      user_pw: '',
      passwordCheck: '',
      logMessage: ''
    }
  },
  methods: {
    fnSignup() {
      if (this.user_id === '') {
        alert('ID를 입력하세요.')
        return
      }

      if (this.user_pw === '') {
        alert('비밀번호를 입력하세요.')
        return
      }

      if (this.user_pw !== this.passwordCheck) {
        alert('비밀번호가 일치하지 않습니다.')
        return
      }

      axios.get("/signup", {
        params : {
          username : this.name,
          userId : this.user_id,
          password : this.user_pw
        }
      })
          .then(response=>{
            console.log(response.data);
            //this.user_pw = response.data;
            alert('회원가입이 완료되었습니다.')
            this.logMessage = `${this.name} 님이 가입되었습니다.`;
            // 가입 후 폼 초기화
            this.initForm();
          })
          .catch(err=>{
            console.log(err.response);
          });

    },
  },
  initForm() {
    this.name = '';
    this.user_id = '';
    this.user_pw = '';
    this.passwordCheck = '';
  },
};

</script>
